@font-face {
    font-family: GlebFont;
    src: url('/glebfont.otf') format("opentype");
}

body {
    font-family: GlebFont;
    background-color: #c3e2da;
}

.App .header {
    color: #f22c16;
    font-size: 4.5vw;
    margin-bottom: 40px;
}

.App .red {
    color: #f22c16;
}

.App .green {
    color: #597a00;
}

.App .lite-green {
    color: #7bb000;
}

@media screen and (max-width: 850px) {
    .App .header {
        font-size: 10vw;
        margin-bottom: 20px;
    }
}
