.Feedback {
    width: 100%;
    
    text-align: center;

    position: relative;
}

.Feedback .background {
    width: 100%;
}

.Feedback .content-wrapper {
    position: absolute;
    top: 10%;
    left: 10%;
    right: 10%;
}

.Feedback .comment {
    font-size: 2vw;
    margin-bottom: 20px;
    text-align: left;
}

.Feedback .comment.right .description,
.Feedback .comment.right .top-line .avatar {
    margin-left: auto;
}

.Feedback .top-line {
    display: flex;
    align-items: center;
    text-decoration: none;
}

.Feedback .description {
    width: 40vw;
    margin-top: 15px;
}

.Feedback .name {
    margin-left: 20px;
    font-size: 3vw;
}

.Feedback .top-line .avatar {
    width: 8vw;
    height: 8vw;
}

.Feedback .show-secondary {
    display: none;

    font-size: 6vw;
    padding: 20px 20px;
}

@media screen and (max-width: 850px) {
    .Feedback .background {
        display: none;
    }

    .Feedback .content-wrapper {
        position: relative;
        top: initial;
        left: initial;
        right: initial;
    }

    .Feedback .comment {
        font-size: 5vw;
        margin: 0 4vw 4vw 4vw;
    }

    .Feedback .comment .name {
        font-size: 7vw;
    }

    .Feedback .top-line .avatar {
        height: 12vw;
        width: 12vw;
    }

    .Feedback .comment.right .description,
    .Feedback .comment.right .top-line .avatar {
        margin-left: initial;
    }

    .Feedback .description {
        width: 100%;
    }

    .Feedback:not(.more) .secondary {
        display: none;
    }

    .Feedback:not(.more) .show-secondary {
        display: inline-block;
    }

    .Feedback:not(.more) .comment:first-child {
        margin: 0 4vw;
    }
}
