.Header {
    width: 100%;
    
    text-align: center;

    position: relative;
}

.Header .background {
    width: 100%;
}

.Header .menu-wrapper {
    text-align: center;
}

.Header .menu {
    display: flex;
    justify-content: space-between;
    align-items: center;

    list-style: none;

    width: 90%;
}

.Header .menu .menu-item {
    display: block;

    width: 140px;
    height: 80px;

    color: #fff;
    background: #007834;

    font-size: 31px;
    border-radius: 20px;

    line-height: 80px;
    cursor: pointer;

    padding: 0 10px;
    margin: 0 10px;

    opacity: .8;
}

.Header .menu .menu-item:nth-child(2n) {
    background: #597a00;
}

.Header .menu .menu-item:hover {
    opacity: 1;
}

@media screen and (max-width: 995px) {
    .Header .menu .menu-item {
        background: none;

        font-size: 33px;

        padding: 0;
        color: #007834;
    }

    .Header .menu .menu-item:nth-child(2n) {
        background: none;
    }

    .Header .menu {
        bottom: 6%;
    }
}

@media screen and (max-width: 500px) {
    .Header .menu-wrapper {
        display: none;
    }
}

